import { BasePattern } from "@patternslib/patternslib/src/core/basepattern";
import Parser from "@patternslib/patternslib/src/core/parser";
import registry from "@patternslib/patternslib/src/core/registry";
import React from "react";
import ReactDOM from "react-dom/client";
import DatePickerComponent from "./DatePicker"; // Import the new React component
import { DatePicker } from "react-aria-components";

export const parser = new Parser("datetime");
parser.addArgument("example-option", "Stranger");

class Pattern extends BasePattern {
    static name = "datetime";
    static trigger = ".pat-datetime";
    static parser = parser;

    async init() {
        import("./collective-datepicker.scss");

        // The options are automatically created, if parser is defined.
        const example_option = this.options.exampleOption;
        console.log("example_option", example_option);
        // Render the React DatePickerComponent into this.el
        const root = ReactDOM.createRoot(this.el);
        console.log("prerender");
        // root.render(<DatePicker label="Date" arial-label="Date" />);
        root.render(<DatePickerComponent label={example_option} />);
        console.log("postrender");
    }
}

// Register Pattern class in the global pattern registry and make it usable there.
registry.register(Pattern);

// Export Pattern as default export.
// You can import it as ``import AnyName from "./collective-datepicker";``
export default Pattern;
// Export BasePattern as named export.
// You can import it as ``import { Pattern } from "./collective-datepicker";``
export { Pattern };
