import React from 'react';
import { DatePicker, DateInput, DateSegment, Button, Calendar, Popover, Dialog, Label } from 'react-aria-components';
import { today, getLocalTimeZone } from '@internationalized/date';

const DatePickerComponent = ({ label }) => {
  const visibleLabel = label || "Select Date";
  return (
    <DatePicker label={visibleLabel} defaultValue={today(getLocalTimeZone())}>
      <Label>{visibleLabel}</Label>
      <DateInput>
        {(segment) => <DateSegment segment={segment} />}
      </DateInput>
      <Button>üóìÔ∏è</Button>
      <Popover>
        <Dialog>
          <Calendar />
        </Dialog>
      </Popover>
    </DatePicker>
  );
};

export default DatePickerComponent;
